/* animation-name: mostrarImagen;
animation-duration: 1s;
animation-timing-function: ease-in-out;
animation-delay: 0s;
animation-iteration-count: 1; 

animation: 1s ease-in-out 0s 1 mostrarImagen;
*/
@keyframes mostrarImagenDerecha {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
@keyframes mostrarImagenizquierda {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}
@keyframes mostrarImagenAbajo {
  0% {
    opacity: 0;
    transform: translateY(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
@keyframes mostrarImagenArriba {
  0% {
    opacity: 0;
    transform: translateY(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 2rem 0;
  background-color: #4CB8B3;
  z-index: 20;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
    padding: 0.5rem 0;
  }
}
.header__grid {
  display: grid;
  place-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__grid {
    display: flex;
    justify-content: space-between;
  }
}
.header__heading {
  font-weight: 700;
  margin: 0;
  text-align: center;
}
.header__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header__nav {
    flex-direction: row;
    gap: 2rem;
  }
}
.header__enlace {
  font-size: 2.4rem;
}
.header__enlace:hover {
  color: #FEF46D;
}

.rellenoPadding {
  padding-top: 9.6rem;
}

.video {
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
  position: relative;
}
.video__video {
  display: block;
  width: auto;
}
@media (min-width: 992px) {
  .video__video {
    width: 100%;
  }
}

.overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.overlay__heading {
  color: #FFFFFF;
  font-size: 7rem;
  font-weight: 700;
  margin: 0;
}
.overlay__ubicacion {
  padding: 0.5rem 1rem;
  margin: 0;
  background-color: #F53756;
  color: #FFFFFF;
  font-size: 2.5rem;
  font-weight: 700;
  display: inline-block;
}

.sobre-festival {
  padding: 5rem 0;
  display: grid;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.sobre-festival__heading {
  font-weight: 700;
}
.sobre-festival__ubicacion, .sobre-festival__texto {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.5;
}
.sobre-festival__ubicacion {
  color: #F53756;
  font-weight: 700;
}

.artistas {
  padding: 5rem 0;
  background-color: #F53756;
}
.artistas__heading {
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
}
.artistas__dia {
  color: #FFFFFF;
  text-align: center;
  font-size: 2.4rem;
  margin: 3rem 0;
}
.artistas__grid {
  display: grid;
}
@media (min-width: 768px) {
  .artistas__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.escenario {
  padding: 2rem 0;
}
.escenario--amarillo {
  background-color: #FEF46D;
}
.escenario--verde {
  background-color: #4CB8B3;
}
.escenario__titulo {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin: 0;
}
.escenario__titulo::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center;
  margin: 1rem auto;
}
.escenario__titulo--rock::after {
  background-image: url(../img/icono_rock.webp);
}
.escenario__titulo--edm::after {
  background-image: url(../img/icono_edm.webp);
}
.escenario__lista--verde li:nth-child(odd) {
  background-color: #3b9691;
}
.escenario__lista--amarillo li:nth-child(odd) {
  background-color: #fdec08;
}
.escenario__li {
  padding: 2.5rem;
  font-weight: 700;
}

.boletos {
  padding: 5rem 0;
  background-color: #4CB8B3;
}
.boletos__heading {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  margin-bottom: 3rem;
}
.boletos__grid {
  width: 95%;
  max-width: 80rem;
  margin: 0 auto;
  display: grid;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .boletos__grid {
    grid-template-columns: repeat(3, 1fr);
    gap: unset;
  }
}

.boleto {
  padding: 2rem;
  background-color: #FFFFFF;
  border-radius: 2rem;
  text-align: center;
  transition-property: transform;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .boleto {
    min-height: 44rem;
  }
  .boleto:hover {
    transform: scale(1.2);
  }
}
.boleto__VIP {
  background-color: #F53756;
  padding: 4rem 2rem;
  color: #FFFFFF;
  grid-row: 1/2;
  z-index: 10;
}
@media (min-width: 768px) {
  .boleto__VIP {
    grid-column: 2/3;
  }
}
.boleto__premium {
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .boleto__premium {
    grid-row: 1/2;
  }
}
.boleto__simple {
  min-height: 52.7rem;
}
.boleto__heading {
  font-weight: 700;
  font-size: 2.6rem;
}
.boleto__precio {
  font-weight: 900;
  font-size: 2rem;
}
.boleto__texto {
  font-weight: 700;
  font-size: 1.8rem;
}
.boleto__enlace {
  font-weight: 400;
  font-size: 1.8;
  padding: 1rem 0;
}
.boleto__boton {
  background-color: #F53756;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boleto__boton {
    width: auto;
  }
}
.boleto__boton:hover {
  background-color: #ed0c31;
}
.boleto__boton--VIP {
  border: 2px solid #FFFFFF;
}
.boleto__boton--simple {
  margin-top: 6rem;
}

.galeria {
  padding: 5rem 0;
}
.galeria__heading {
  text-align: center;
  font-weight: 700;
}
.galeria__imagenes {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 480px) {
  .galeria__imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .galeria__imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 992px) {
  .galeria__imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(6, 1fr);
  }
}

.ventanaModal {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.ventanaModal__imagen {
  display: block;
  max-width: 80rem;
}
.ventanaModal__btn {
  padding: 1rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  font-size: 2rem;
  font-weight: 900;
  color: #000000;
  transition-property: background-color, color;
  transition-duration: 0.3s;
}
.ventanaModal__btn:hover {
  cursor: pointer;
  background-color: #000000;
  color: #FFFFFF;
}

.modal {
  overflow: hidden;
}

.footer {
  padding: 1rem 0;
  background-color: #F53756;
  color: #FFFFFF;
}
.footer__copy {
  margin: 0;
  text-align: center;
  font-size: 2rem;
}